import React  from 'react';
import {SocialMedia,Social,Icon,SocialDesc,Span,SpanInfo } from './style.js'
import axios from 'axios'
class SocailMedia extends  React.Component {

  state = {
    social : []
  }
  componentDidMount (){
        
    axios.get('js/data.json').then( res=> 
      
          { this.setState({social : res.data.social})
      })
    }
  render() {
    
    const {social} = this.state
    const sociaList = social.map((socialItem) => {
      console.log("========socialItemsocialItem===>", socialItem.icon)
      
      return(
        <Social item={socialItem.id} key={socialItem.id} >
        <Icon className={socialItem.icon}></Icon>
        <SocialDesc>
            <Span>{socialItem.title}</Span>
            <SpanInfo>{socialItem.body}</SpanInfo>
        </SocialDesc>
        
    </Social>
      )
})

    return (
      <SocialMedia>
        {sociaList}
        </SocialMedia>
    )

  }
}




export default SocailMedia;